.benner {
  width: 100%;
}

.slick-prev:before {
  content: "";
}

.slick-next:before {
  content: "";
}

.product {
  h3 {
    font-family: "Roboto", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    /* identical to box height */
    color: #313132;
    margin-bottom: 15px;
  }

  h5 {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    /* identical to box height */

    color: #313132;
    margin-top: 20px;
  }

  .brand {
    .col-md-4 {
      padding: 0 20px;
    }
  }
}

.new-arrival {
  margin: 30px 0;

  .new-arrival-carousel {
    width: 95%;
    margin: 0 auto;

    .new-arrival-carousel-card {
      // width: 218px;
      margin: 0 15px;

      img {
        margin: 0 auto;
        width: 100%;
        max-height: 100%;
        height: 250px;
        object-fit: contain;
        border-radius: 8px;
      }

      h4 {
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 21px;
        color: #313132;
      }

      p {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        color: #313132;
      }
    }
  }
}

#product-detail {
  .product-detail-content {
    .desc {
      font-style: normal;
      color: #313132;

      h4 {
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
      }

      h5 {
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
      }

      p {
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: #c4c4c4;
      }

      ul {
        list-style: none;
        padding: 0;
        li {
          padding: 0 0 5px 40px;
          position: relative;
          margin: 0;
        }

        li:after {
          position: absolute;
          top: 0;
          left: 0;
          content: "";
          // counter-increment: steps;
          border: 2px solid #000;
          border-radius: 50%;
          display: inline-block;
          height: 10px;
          width: 10px;
          margin: 5px;
          text-align: center;
          line-height: 24px;
          background: #000;
        }

        li:before {
          position: absolute;
          left: 9px;
          top: 5px;
          content: "";
          height: 100%;
          width: 0;
          border-left: 2px solid #000;
        }
        li:last-of-type:before {
          border: none;
        }
      }
    }

    .product-img {
      .slick-slider {
        .slick-dots {
          text-align: start;
          position: static;

          li {
            width: 10%;
            height: 100%;
            filter: grayscale(100%);
          }

          :first-child {
            margin-left: 0;
          }

          .slick-active {
            filter: grayscale(0%);
          }
        }
      }
    }
  }

  .similar-product {
    font-style: normal;

    h3 {
      font-family: "Roboto", sans-serif;

      font-weight: 700;
      font-size: 16px;
      line-height: 19px;
      /* identical to box height */

      color: #313132;
    }

    .similar-product-carousel {
      padding: 0 10px;

      .similar-product-carousel-card {
        margin: 0 10px;
        font-family: "Poppins", sans-serif;

        line-height: 21px;

        h4 {
          font-weight: 700;
          color: #313132;
          font-size: 14px;
          margin-top: 20px;
          margin-bottom: 5px;
        }
        p {
          font-weight: 400;
          font-size: 14px;
          color: #313132;
        }
      }
    }
  }
}

#product-option {
  h4 {
    margin-bottom: 21px;
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    /* identical to box height */

    color: #313132;
  }

  p {
    margin-top: 10px;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    /* identical to box height */

    color: #313132;
  }

  .version {
    .row {
      .col-md-3 {
        .active {
          img {
            border: 2px solid #17949d;
            border-radius: 8px;
          }
        }
      }
    }
  }

  .version,
  .category,
  .color,
  .covering {
    margin-bottom: 20px;
  }

  label {
    font-family: "Roboto", sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    /* identical to box height */

    color: #313132;
    margin-bottom: 11px;
  }
}

.qty-form {
  .decrease {
    text-decoration: none;
    border: 0.5px solid #c4c4c4;
    width: 28px;
    background-color: #ffffff;
    border-radius: 4px 0px 0px 4px;
    height: 28px;
  }

  .qty-input {
    width: 28px;
    border: 0.5px solid #c4c4c4;
    background: #fff;
    height: 28px;
  }

  .increase {
    text-decoration: none;
    border: 0.5px solid #c4c4c4;
    width: 28px;
    background-color: #17949d;
    color: #ffffff;
    border-radius: 0px 4px 4px 0px;
    height: 28px;
  }
}

