        line-height: 21px;
        /* identical to box height */

        color: #313132;
      }

      p {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        /* identical to box height */

        color: #313132;
      }
    }
  }
}

