.product-image-galery {
  .product-images:first-child {
  }
}

#landing-product {
  section {
    p {
      font-family: century-gothic, sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
      /* or 150% */

      color: #a6a6a6;
    }

    h3 {
      font-family: "Playfair Display", serif;
      font-style: normal;
      font-weight: 700;
      font-size: 24px;
      line-height: 32px;
      /* identical to box height */

      color: #313132;
    }

    h4 {
      font-family: century-gothic, sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 22px;
      /* identical to box height */

      color: #313132;
    }

    h5 {
      font-family: century-gothic, sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 14px;
      line-height: 21px;
      /* identical to box height, or 150% */

      color: #272727;
    }

    .brand-name,
    .model-name {
      margin-bottom: 12px;
    }

    .price {
      margin-bottom: 39px;
    }

    .desc {
      margin-bottom: 34px;
    }

    .button-product {
      margin-bottom: 68px;
    }
  }
  footer {
    margin-bottom: 58px;
    h3 {
      font-family: "Playfair Display", serif;
      font-style: normal;
      font-weight: 700;
      font-size: 24px;
      line-height: 150%;
      /* identical to box height, or 36px */

      color: #272727;
      margin-bottom: 24px;
    }
    p {
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      /* identical to box height, or 21px */
      margin-bottom: 0;
      color: #272727;
    }
  }
}

.similar-product {
  font-style: normal;
  padding: 35px;

  .similar-product-carousel {
    padding: 0 10px;

    .similar-product-carousel-card {
      margin: 0 10px;
      font-family: "Poppins", sans-serif;

      line-height: 21px;

      h3 {
        font-size: 16px !important;
        color: #272727 !important;
      }
      p {
        font-weight: 400;
        font-size: 14px;
        color: #313132;
      }
    }
  }
}
