#customer-card {
  background: #ffffff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
  border-radius: 8px;
  padding: 30px;
  margin: 10px 0;
}

.customer-img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: #17949d;
  margin-right: 20px;
  margin-left: 12px;
}

.customer-img h2 {
  font-weight: 700;
  font-size: 24px;
  line-height: 54px;
  /* identical to box height */
  margin: 0;
  color: #ffffff;
}

.customer-desc {
  font-style: normal;
  color: #313132;
  margin: 0;
}

.customer-card h2 {
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
}



.customer-card h4 {
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  /* identical to box height */
  margin-bottom: 20px;
  color: #313132;
}

.customer-desc p {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  margin-bottom: 10px;
}
