@import url("https://fonts.googleapis.com/css2?family=League+Spartan:wght@300;400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Marcellus&display=swap");
#landing-product-dio-living {
  section {
    p {
      font-family: "League Spartan", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
      /* or 150% */

      color: #a6a6a6;
    }

    h3 {
      font-family: "Marcellus", serif;
      font-style: normal;
      font-weight: 400;
      font-size: 24px;
      line-height: 32px;
      /* identical to box height */

      color: #313132;
    }

    h4 {
      font-family: "League Spartan", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 22px;
      /* identical to box height */

      color: #313132;
    }

    h5 {
      font-family: "League Spartan", sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 14px;
      line-height: 21px;
      /* identical to box height, or 150% */

      color: #272727;
    }

    .brand-name,
    .model-name {
      margin-bottom: 12px;
    }

    .price {
      margin-bottom: 39px;
    }

    .desc {
      margin-bottom: 34px;
    }

    .button-product {
      margin-bottom: 68px;
    }
  }

  footer {
    h3 {
      font-family: "Marcellus", serif;
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 23px;
      /* identical to box height, or 36px */

      color: #e5e5e5;
      margin-bottom: 24px;
    }
    p {
      font-family: "League Spartan", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;
      /* identical to box height, or 21px */
      margin-bottom: 0;
      color: #e5e5e5;
    }
  }
}
