.card {
  width: 100%;
  height: 275px;
  border-radius: 8px;
  outline: 1px solid rgba(255, 255, 255, 0.5);
  outline-offset: -8px;
  position: relative;
  overflow: hidden;
}

.card-image{
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform .75s;
}

.card:hover .card-image {
  transform: scale(1.15);
}
