.header {
  margin-bottom: 10px;

  .header-text {
    h1 {
      font-style: normal;
      font-weight: 700;
      font-size: 36px;
      line-height: 54px;
      /* identical to box height */

      color: #313132;
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    .header-text {
      h1 {
        font-size: 19px;
      }
    }
  }

  .user-desc {
    display: none;
  }
}

.header-user {
  h2 {
    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    line-height: 24px;
    margin: 0;
  }
  .user-img {
    padding: 12px;
    background-color: #17949d;
    border-radius: 50%;
    margin: 0 15px;

    h2 {
      color: #ffffff;
    }
  }
}

.notification-card {
  position: absolute;
  right: 59px;
  top: 108px;
  background: #ffffff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
  border-radius: 8px;
  padding: 20px;
  width: 500px;
  display: none;
}

.notif-active {
  display: block;
}
