#report {
  .filter-form {
    .header {
      img {
        width: 17px;
        height: 22px;
        margin-right: 13px;
      }

      h3 {
        font-style: normal;
        font-weight: 700;
        font-size: 15px;
        line-height: 24px;
        margin: 0;
        color: #313132;
      }
    }

    .report-filter-form {
      .form-select,
      .form-control {
        margin-left: 10px;
        height: 41px;
      }
    }
  }

  @media screen and (max-width: 768px) {
    .filter-form {
      flex-direction: column;
    }

    .report-filter-form {
      flex-direction: column;

      .form-select,
      .form-control {
        margin: 5px 0;
        margin-left: 0 !important;
      }
    }
  }

  .report-cards {
    font-family: "Poppins";
    font-style: normal;
    margin: 10px 0;

    .report-card {
      margin: 10px 0;
      .report-card-wrapper {
        height: 160px;
        background: #ffffff;
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
        border-radius: 8px;
        padding: 24px;

        h5 {
          font-weight: 400;
          font-size: 13px;
          line-height: 21px;
          color: #c4c4c4;
        }

        h4 {
          font-weight: 700;
          font-size: 19px;
          line-height: 30px;
          color: #313132;
        }

        p {
          font-weight: 400;
          font-size: 13px;
          line-height: 21px;
          color: #313132;
        }

        .percent {
          font-weight: 400;
          font-size: 17px;
          line-height: 27px;
          /* identical to box height */

          color: #2dcc70;
        }
      }
    }
  }
}

.report {
  margin: 20px 0;
  .col-md-4:first-child {
    padding-left: 0;
    padding-right: 24px;
  }

  .col-md-4:last-child {
    padding-left: 24px;
    padding-right: 0;
  }
}

@media screen and (max-width: 768px) {
  .report {
    margin: 20px 0;
    .col-md-4 {
      padding: 0;
    }

    .col-md-4:first-child {
      padding-left: 0;
      padding-right: 0;
    }

    .col-md-4:last-child {
      padding-left: 0;
      padding-right: 0;
    }
  }
}

.form-select {
  font-size: 13px;
}

table {
  border-collapse: collapse;
  /* border-radius: 1em 1em 0 0; */
  overflow: hidden;
  width: 100%;
  margin: 15px 0;

  th {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 700;
    font-size: 13px;
    line-height: 21px;
    /* identical to box height */
    color: #ffffff;
    background: #17949d;
  }

  @media screen and (max-width: 768px) {
    th,
    td {
      padding: 8px !important;
      font-size: 10px !important;
    }
  }

  tr:nth-child(even) {
    background-color: #eaeaea;
  }

  td {
    font-family: "Poppins";
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 21px;
    /* identical to box height */
    color: #313132;
    border-bottom: 1px solid #c4c4c4;

    a {
      text-decoration: none;
      color: #17949d;
    }
  }

  th,
  td {
    padding: 1em;
    /*   background: #ddd; */
    /*   border-bottom: 2px solid #000;  */
  }

  thead th:last-child {
    border-radius: 0 8px 8px 0;
  }

  thead th:first-child {
    border-radius: 8px 0 0 8px;
  }
}

@media screen and (max-width: 768px) {
  .bum-table,
  .channel-table,
  .sales-table {
    overflow: auto;
  }

  .top-ranking {
    flex-direction: column;
  }
}

.css-mine {
  margin-top: 2em;
  clear: both;
}

.progress {
  background-color: #fff;
}

.progress-bar {
  background-color: #17949d;
}

