#comparationv2 {
  .report-cards {
    .col-md-4 {
      .sales-revenue,
      .sattlement,
      .interior-design {
        background: #ffffff;
        box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
        border-radius: 8px;
        margin-bottom: 24px;

        .report-card {
          width: 100%;
          .report-card-wrapper {
            border-radius: 8px;
            padding: 24px;

            h5 {
              font-weight: 400;
              font-size: 13px;
              line-height: 21px;
              color: #c4c4c4;
            }

            span {
              border-radius: 4px;
              padding: 4px 10px;
              color: #fff;
              margin-left: 5px;
            }

            h4 {
              font-weight: 700;
              font-size: 19px;
              line-height: 30px;
              color: #313132;
            }

            p {
              font-weight: 400;
              font-size: 13px;
              line-height: 21px;
              color: #313132;
            }

            .percent {
              font-weight: 400;
              font-size: 17px;
              line-height: 27px;
              /* identical to box height */

              color: #2dcc70;
            }
          }
        }
      }
    }
  }
}
